import { Notify } from 'notiflix/build/notiflix-notify-aio';
import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

import { searchImages } from './SearchImages'
import { cardMacker } from './card';

const searchFormEl = document.querySelector('.search-form');
const galleryEl = document.querySelector('.gallery');
const loaderEl = document.querySelector('.loader');
const mainBackgrounEL = document.querySelector('.background-image');
const filterEl = document.querySelector('.filters');

let hasMorePictures;
let name;
const options = {
    root: null,
    rootMargin: '200px',
    threshold: 1
}

let gallery;
const arrOptionArgument = {
    name: '',
    page: 1,
    per_page: 40,
    category: 'all',
    colors: 'all',
    orientation: 'all',
    image_type: 'all',
};

const callbackObserv = function (entries, observer) {
    entries.forEach(entry => {
        if (entry.isIntersecting) {
            observer.unobserve(entry.target)
            console.log('observer -   observer')
            arrOptionArgument.page += 1;
            loaderEl.classList.remove('visualy-hidden')
            searchImages(arrOptionArgument)
                .then(({ hits, total }) => {
                    insertPicture(hits, total); //   + 
                    loaderEl.classList.add('visualy-hidden')
                    gallery.refresh() //   SimpleLightbox
                });
        }
    });
};

const observer = new IntersectionObserver(callbackObserv, options);

function hendleSearchImages(e) {
    e.preventDefault();
    name = e.target.elements.searchQuery.value.trim();
    if (name === '') {
        return Notify.failure('Enter the search request');
    } else {
        loaderEl.classList.remove('visualy-hidden');
        arrOptionArgument.name = name;
        searchImages(arrOptionArgument)
            .then(({ hits, total }) => {
                if (hits.length === 0) {
                    searchFormEl.reset(); //    input
                    return Notify.failure(`Sorry, there are no images with name '${name}' matching your search query. Please try again.`);
                }
                searchFormEl.classList.add('js--search-form'); //     .
                mainBackgrounEL.classList.add('is-hidden');//   ,     
                hasMorePictures = total > (40 * arrOptionArgument.page);//    observer
                insertPicture(hits, total);//  +   observer
                Notify.success(`Hooray! We found ${total} images.`);
                loaderEl.classList.add('visualy-hidden');//  3  
                setTimeout(() => filterEl.classList.remove('is-hidden'), 1000)//              ,    setTimeout,      ,    
            })
            .catch((err) => {
                throw new Error(err);
            })
    }
}

searchFormEl.addEventListener('submit', hendleSearchImages);

function hendleSearchImagesByFilter(e) { //          +   
    arrOptionArgument.page = 1;
    loaderEl.classList.remove('visualy-hidden');
    switch (e.target.name) {
        case 'category':
            arrOptionArgument.category = e.target.value;
            searchImages(arrOptionArgument)
                .then(({ hits, total }) => {
                    insertPicture(hits, total);
                    Notify.success(`Hooray! We found ${total} images.`);
                })
                .catch((err) => {
                    throw new Error(err);
                })
            break;
        case 'colors':
            arrOptionArgument.colors = e.target.value;
            searchImages(arrOptionArgument)
                .then(({ hits, total }) => {
                    insertPicture(hits, total);
                    Notify.success(`Hooray! We found ${total} images.`);
                })
                .catch((err) => {
                    throw new Error(err);
                })
            break;
        case 'orientation':
            arrOptionArgument.orientation = e.target.value;
            searchImages(arrOptionArgument).then(({ hits, total }) => {
                insertPicture(hits, total);
                Notify.success(`Hooray! We found ${total} images.`);
            })
                .catch((err) => {
                    throw new Error(err);
                })
            break;
        case 'image type':
            arrOptionArgument.image_type = e.target.value;
            searchImages(arrOptionArgument).then(({ hits, total }) => {
                insertPicture(hits, total);
                Notify.success(`Hooray! We found ${total} images.`);
            })
                .catch((err) => {
                    throw new Error(err);
                })
            break;
    }
}

function insertPicture(hits, total) { //      ,     .
    hasMorePictures = arrOptionArgument.page < Math.ceil(total / 40);//   hasMorePictures  observer
    const cardMarkup = cardMacker(hits);
    if (arrOptionArgument.page === 1) { //  ,     (   ),   
        galleryEl.innerHTML = cardMarkup;
        window.scrollTo(0, 0); //         ,  40,      24 
        loaderEl.classList.add('visualy-hidden');
    } else {
        galleryEl.insertAdjacentHTML('beforeend', cardMarkup);
        loaderEl.classList.add('visualy-hidden');
    }

    if (hasMorePictures) {
        observer.observe(document.querySelector('.photo-card:last-child'));//  
        console.log('insertPicture -   observer')
    } else {
        Notify.info(`The pictures are gone(`);
    }
    gallery = new SimpleLightbox('.photo-card__link', { //    (      )
        captions: true,
        captionDelay: 250,
        captionsData: 'alt',
        captionPosition: 'bottom',
    });
}

searchFormEl.addEventListener('input', hendleSearchImagesByFilter); //  

